html {
  min-height: 100%;
  flex-direction: column;
  display: flex;
}

body {
  width: 100%;
  padding: 0;
  padding-bottom: calc(var(--footer-height) * 1.5);
  background-color: var(--color-bg);
  color: #fff;
  flex-grow: 1;
  margin: 0;
  font-family: Open Sans, sans-serif;
  position: relative;
}

:root {
  --color-bg: #1b0533;
  --color-line: #8d4ad3;
  --color-int: #00b400;
  --footer-height: 100px;
}

.color-int-border {
  border-color: var(--color-int);
}

a {
  -webkit-text-decoration-skip-ink: auto;
  text-decoration-skip-ink: auto;
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: var(--color-int);
}

.navbar {
  height: 90px;
  background-color: #fff;
  flex-direction: row;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 1px 2px #ffffff12, 0 2px #ffffff12, 0 4px 8px #ffffff12, 0 8px 16px #ffffff12, 0 16px 32px #ffffff12, 0 32px 64px #ffffff12;
}

.navbar-logo {
  height: 50%;
  aspect-ratio: 1;
  color: #000;
  fill: none;
  margin-top: auto;
  margin-bottom: auto;
}

.navbar-logo a {
  width: 100%;
  height: 100%;
  display: block;
}

.navbar-logo a svg {
  width: 100%;
  height: 100%;
}

.navbar-title {
  color: #000;
  align-self: center;
  margin-left: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.navbar-list {
  height: 100%;
  flex-direction: row;
  align-items: center;
  margin: 0 0 0 auto;
  list-style: none;
  display: flex;
  padding: 0 !important;
}

.navbar-list li > a {
  color: #555;
  padding-left: .85rem;
  padding-right: .85rem;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: color .2s, transform .2s;
  display: block;
}

.navbar-list li > a.active {
  color: var(--color-int);
  -webkit-text-decoration: underline solid var(--color-int) 2px;
  text-decoration: underline solid var(--color-int) 2px;
  cursor: default;
}

.navbar-list li > a:hover:not(.active) {
  color: var(--color-bg);
  transition: color .2s, transform .2s;
  transform: scale(1.1);
}

.navbar-toggler:focus {
  outline: -webkit-focus-ring-color auto 1px;
  outline-color: -webkit-focus-ring-color;
  box-shadow: none;
  outline-width: 1px;
  outline-style: auto;
}

footer {
  max-height: var(--footer-height);
  width: 100%;
  text-align: center;
  color: #fff;
  justify-content: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

footer .footer-container {
  flex-direction: column;
  align-self: center;
  font-weight: 700;
  display: flex;
}

footer .footer-container span {
  padding: .5rem 0;
}

footer .footer-container svg {
  width: 24px;
  height: 24px;
}

@media only screen and (max-width: 1095px) {
  section#index {
    max-width: 840px;
  }
}

@media only screen and (min-width: 1100px) {
  section#index {
    padding-left: 9rem;
  }
}

@media only screen and (min-width: 1042px) {
